<template>
  <div class="square">
    <van-nav-bar>
      <template #title>
        <div class="square-title">评论广场</div>
      </template>
      <template #left>
        <div class="right-icon">
          <img
            src="../assets/images/left.png"
            alt
            class="auto-img"
            @click="goback"
          />
        </div>
      </template>
    </van-nav-bar>
    <div class="square-info" v-for="(item, index) in squareList" :key="index">
      <div class="square-info-list">
        <div class="square-info-list-person clearfix">
          <div class="square-info-headimg fl">
            <img :src="item.headImg" alt class="auto-img headimg" />
          </div>
          <div class="square-info-name fl">{{ item.userName }}</div>
          <div class="square-info-rank fl">LV{{ item.rank }}</div>
        </div>
        <div class="square-info-text">
          {{ item.text }}
        </div>
        <div class="icons clearfix">
          <div class="icons-left fl clearfix">
            <div class="book-icon fl">
              <img
                src="../assets/images/book-icon.png"
                alt
                class="auto-img small-icon"
              />
            </div>
            <div class="book-name fl">{{ item.bookName }}</div>
          </div>
          <div class="icons-left fr clearfix">
            <div class="icon-comment fl">
              <img
                src="../assets/images/goodping.png"
                alt
                class="auto-img small-icons"
              />
            </div>
            <div class="comment-number fl">{{ item.goodComment }}</div>
            <div class="icon-comment fl">
              <img
                src="../assets/images/badping.png"
                alt
                class="auto-img small-icons"
              />
            </div>
            <div class="comment-number fl">{{ item.badComment }}</div>
            <div class="icon-comment fl">
              <img
                src="../assets/images/pingicon.png"
                alt
                class="auto-img small-icons"
              />
            </div>
            <div class="comment-number fl">{{ item.comment }}</div>
          </div>
        </div>
      </div>
      <div class="line"></div>
    </div>
  </div>
</template>

<script>
import squareList from "../assets/js/discuss";
export default {
  data() {
    return {
      //评论广场
      squareList: squareList.squareList,
    };
  },
  methods: {
    goback() {
      this.$router.back();
    },
  },
};
</script>

<style lang="less" scoped>
.square {
  .right-icon {
    width: 24px;
    height: 24px;
  }

  .square-title {
    font-size: 16px;
    color: #016831;
  }

  /deep/ .van-nav-bar {
    background: #e8dddb;
  }

  .line {
    width: 100%;
    height: 10px;
    background: #e8dddb;
  }

  .square-info-list {
    padding: 15px 20px;
    background: #eee3e1;
  }

  .square-info-list-person {
    width: 100%;
    height: 40px;
  }

  .square-info-headimg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
  }

  .headimg {
    height: 40px;
  }

  .square-info-name {
    height: 20px;
    padding: 0 5px;
    margin-top: 10px;
    line-height: 20px;
    color: #016831;
    font-size: 12px;
  }

  .square-info-rank {
    width: 35px;
    height: 15px;
    line-height: 15px;
    border: 1px solid #aba09d;
    border-radius: 10px;
    color: #aba09d;
    text-align: center;
    margin-top: 11px;
  }

  .square-info-text {
    padding: 15px 0px 0px 0px;
    font-size: 12px;
    line-height: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 6;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    letter-spacing: 1px;
  }

  .book-icon {
    width: 24px;
    height: 24px;
  }

  .icons {
    padding: 10px 0px 0px 0px;
  }

  .small-icon {
    height: 24px;
  }

  .small-icons {
    height: 20px;
  }

  .icon-comment {
    width: 20px;
    height: 20px;
    margin: 2px 0px;
  }

  .book-name {
    padding-left: 5px;
    line-height: 24px;
    color: #016831;
  }

  .comment-number {
    width: 30px;
    text-align: center;
    color: #aba09d;
    line-height: 24px;
  }
}
</style>